import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

/*
  Generated class for the NudriveProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class NudriveProvider {

  constructor(public http: HttpClient) {
    console.log('Hello NudriveProvider Provider');
  }



  // signin () {

  //   this.http.get('https://jsonplaceholder.typicode.com/photos').subscribe(
  //     res => {
  //       console.log(res);
  //     }
  //   );
  // }

  signin (username: string, password:string) {
    return new Promise(resolve => {
      this.http.get('nudrive.azurewebsites.net/account/signin?username='+username+ '&password='+password, {withCredentials: true}).subscribe(data => {
        resolve(data);
      }, err => {
        console.log(err);
      });
    });
  }

  fetchcustomer (nuban: string) {
    return new Promise(resolve => {
      this.http.get('http://localhost:65199/account/fetch?nuban='+nuban, {withCredentials: true}).subscribe(data => {
        resolve(data);
      }, err => {
        console.log(err);
      });
    });
  }

  lockbalance (nuban: string, fakebal: string) {
    return new Promise(resolve => {
      this.http.get('http://localhost:65199/account/lock?nuban='+nuban+'&mask='+fakebal, {withCredentials: true}).subscribe(data => {
        resolve(data);
      }, err => {
        console.log(err);
      });
    });
  }

  signup (nuban: string, name: string , phone: string, bvn: string, password: string) {
    return new Promise(resolve => {
      this.http.get('http://localhost:65199/account/signup?nuban='+nuban+'&name='+name+ '&phone='+phone+'&balance=0'+'&bvn='+bvn+'&password='+password, {withCredentials: true}).subscribe(data => {
        resolve(data);
      }, err => {
        console.log(err);
      });
    });
  }

  transfer (source: string, destination: string, amount: string , description: string) {
    return new Promise(resolve => {
      this.http.get('http://localhost:65199/account/transfer?source='+source+'&destination='+destination+'&amount='+amount+'&description='+description, {withCredentials: true}).subscribe(data => {
        resolve(data);
      }, err => {
        console.log(err);
      });
    });
  }




}



