import { Geolocation } from '@ionic-native/geolocation';

declare var google;

@Component({
  selector: 'home-page',
  templateUrl: 'home.html'
})
export class HomePage {

  @ViewChild('map') mapElement: ElementRef;
  map: any;

  getdevicepostion: any;
  logoutresponse: any;
  loggedin = false;

  ref = this.afDB.database.ref('nudriverlocation/');
  constructor(public navCtrl: NavController,private Service: MyservicesProvider, private storage: Storage, public geolocation: Geolocation, private device: Device, private afDB: AngularFireDatabase) {




  }

  ionViewDidEnter() {
    this.loadMap();
  }

  loadMap(){



// let latLng = new google.maps.LatLng(3.3632583, 6.5697245);
// let mapOptions = {
//   center: latLng,
//   zoom: 15,
//   // disableDefaultUI: true,
//   mapTypeId: google.maps.MapTypeId.ROADMAP
//   }
//   this.map = new google.maps.Map(this.mapElement.nativeElement, mapOptions);


// this.getdevicepostion = this.geolocation.watchPosition().subscribe
//     // .filter((p) => p.coords !== undefined) //Filter Out Errors
//     (position => {
// console.log(position.coords.longitude + ' ' + position.coords.latitude);

// let latLng = new google.maps.LatLng(position.coords.latitude, position.coords.longitude);
let latLng = new google.maps.LatLng(3.3632583, 6.5697245);
let mapOptions = {
  center: latLng,
  zoom: 15,
  disableDefaultUI: true,
  mapTypeId: google.maps.MapTypeId.ROADMAP
  }
  this.map = new google.maps.Map(this.mapElement.nativeElement, mapOptions);



// this.updateGeolocation(this.device.uuid, position.coords.latitude,position.coords.longitude);
// add marker after getting location
if(latLng!= null) {
  console.log(latLng);
  this.addMarker(latLng);
}


// }, (err) => {
// console.log(err);
// });
// let myLatLngg = new google.maps.LatLng({lat: 3.4447962, lng: 6.1653944});

// // this.addMarker(myLatLngg);
// console.log(myLatLngg);


}






  addMarker(latlong){

    let marker = new google.maps.Marker({
      position: latlong,
      map: this.map,
      animation: google.maps.Animation.DROP
    });

    let content = "<h4>Information!</h4>";
    marker.setPosition(latlong);
    this.addInfoWindow(marker, content);

  }

  addInfoWindow(marker, content){

    let infoWindow = new google.maps.InfoWindow({
      content: content
    });

    google.maps.event.addListener(marker, 'click', () => {
      infoWindow.open(this.map, marker);
    });

  }

  ionViewDidLeave() {

  }


  viewrequests()
  {
    this.navCtrl.push(RidersrequestPage);

  }

  logout () {
    this.storage.get('usernamedriver').then(username => {
      this.Service.logout(username).then(data => {

        this.logoutresponse = data;
        this.storage.remove('usernamedriver').then(() => {
          console.log('username has been removed'+ this.logoutresponse.Response );
          this.navCtrl.setRoot(LoginPage);
          this.loggedin = false;
        });

      });

    });

  }
  addMarkerr() {}

  updateGeolocation(uuid, lat, lng) {
    if(localStorage.getItem('mykey')) {

      this.afDB.database.ref('nudriverlocation/'+localStorage.getItem('mykey')).set({
        uuid: uuid,
        latitude: lat,
        longitude : lng
      });
    } else {
      let newData = this.ref.push();
      newData.set({
        uuid: uuid,
        latitude: lat,
        longitude: lng
      });
      localStorage.setItem('mykey', newData.key);
    }

    console.log(uuid);
  }


}



