import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, AlertController } from 'ionic-angular';
import { MyservicesProvider } from './../../providers/myservices/myservices';
/**
 * Generated class for the RidersrequestPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-ridersrequest',
  templateUrl: 'ridersrequest.html',
})
export class RidersrequestPage {

  fetchdriverbooks: any;
  constructor(public navCtrl: NavController, public navParams: NavParams,private alertCtrl: AlertController, private Service: MyservicesProvider, public loadingCtrl: LoadingController) {
  this. fetchdriverriderequest();

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad RidersrequestPage');
  }


  fetchdriverriderequest() {
      // console.log(loginval);
   this.Service.fetchbooked('NUDRIVE_6581').then(data => {
    this.fetchdriverbooks = data;
     console.log(this.fetchdriverbooks);

     if(this.fetchdriverbooks.length != 0) {
     this.presentLoadingDefault();

    //  console.log(this.signined.Response);
     }

     else {
     this.presentAlert();

     }

  });
  }


  presentAlert() {
    let alert = this.alertCtrl.create({
      title: 'No Ride Request',
      subTitle: 'Please Check Back!',
      buttons: ['Dismiss']
    });
    alert.present();
  }

  presentLoadingDefault() {
    let loading = this.loadingCtrl.create({
      content: 'Hang In Tight, Signing you in....'
    });

    loading.present().then(() => {
      loading.dismiss();
      // this.navCtrl.setRoot(HomePage);

    });;


  }

}



