import { MyservicesProvider } from './../../providers/myservices/myservices';
import { ListPage } from './../list/list';
import { HomePage } from './../home/home';
import { Component } from '@angular/core';
import { AlertController } from 'ionic-angular';
import { IonicPage, NavController,LoadingController, NavParams, Platform} from 'ionic-angular';
import { SignupPage } from '../signup/signup';
import { Storage } from '@ionic/storage';

/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {
usernamee: string;
passwordd: string;
signined: any;
local:any;
loggedin = false;
  constructor(public navCtrl: NavController,public platform: Platform, public navParams: NavParams, private Service: MyservicesProvider, private alertCtrl: AlertController, public loadingCtrl: LoadingController,private storage: Storage) {
    this.storage.get('usernamedriver').then(data=>
      {
        if(data)
        {
          this.loggedin = true;
          console.log("loogedin already"+ data);
          this.navCtrl.push(HomePage);
        }
        else {
          console.log("not logedin");
        }

        });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LoginPage');


  }

  logmein() {
    this.navCtrl.setRoot(HomePage);
  }

  gotoregister(){
    this.navCtrl.push(SignupPage);
    console.log('I was clicked');
  }

  onsubmitpin(loginval: any){

    // console.log(loginval);
   this.Service.signin(loginval.username , loginval.password).then(data => {
    this.signined = data;
     console.log(this.signined.Response);

     if(this.signined.Response == 'Success') {
     this.presentLoadingDefault();
     this.loggedin == true;
     this.storage.set('usernamedriver', loginval.username);
     this.storage.set('loggedinn', this.loggedin);

     this.storage.get('usernamedriver');
     this.storage.get('loggedinn');
     console.log(this.local.get('usernamedriver')+ ' '+ this.local.get('loggedinn'));
     }

     else {
     this.presentAlert();

     }

    //  this.navCtrl.setRoot(HomePage, {'nuban': loginval.username});

    //  if(this.signined.Response == 'Success') {
    //     //use the nuban to fetch user details
    //   this.myservice.fetchcustomer(loginval.username).then(data => {

    //     console.log(data);

    //   });
    //  }
  });

   };

   presentAlert() {
    let alert = this.alertCtrl.create({
      title: 'Invalid Login',
      subTitle: 'Invalid Username or Password',
      buttons: ['Dismiss']
    });
    alert.present();
  }


  presentLoadingDefault() {
    let loading = this.loadingCtrl.create({
      content: 'Hang On Tight, Signing you in....'
    });

    loading.present().then(() => {
      loading.dismiss();
      this.navCtrl.setRoot(HomePage);

    });;


  }

  closeap() {
    this.exit();
    }

    exit(){
      let alert = this.alertCtrl.create({
        title: 'Confirm',
        message: 'Do you want to exit?',
        buttons: [{
          text: "exit?",
          handler: () => { this.exitApp() }
        }, {
          text: "Cancel",
          role: 'cancel'
        }]
      })
      alert.present();
    }
    exitApp(){
    this.platform.exitApp();
    }


}



