import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Network } from '@ionic-native/network';

/*
  Generated class for the MyservicesProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class MyservicesProvider {

  constructor(public http: HttpClient, private network: Network) {
    console.log('Hello MyservicesProvider Provider');
  }


  signin (username: string, password:string) {
    return new Promise(resolve => {
      this.http.get('http://nudrive.azurewebsites.net/drivers/login?username='+username+ '&password='+password, {withCredentials: true}).subscribe(data => {
        resolve(data);
      },
      err => {
        console.log(err);
      });
    });
  }

  fetchcustomer (nuban: string) {
    return new Promise(resolve => {
      this.http.get('http://localhost:65199/account/fetch?nuban='+nuban, {withCredentials: true}).subscribe(data => {
        resolve(data);
      }, err => {
        console.log(err);
      });
    });
  }

  lockbalance (nuban: string, fakebal: string) {
    return new Promise(resolve => {
      this.http.get('http://localhost:65199/account/lock?nuban='+nuban+'&mask='+fakebal, {withCredentials: true}).subscribe(data => {
        resolve(data);
      }, err => {
        console.log(err);
      });
    });
  }

  signup (username: string, name:string, phone:string, address:string, password:string, vehicle_brand:string, plate_number:string, max_capacity: string) {
    return new Promise(resolve => {
      this.http.get('http://nudrive.azurewebsites.net/drivers/signup?username='+username+'&name='+name+ '&phone='+phone+'&address='+address+'&password='+password+'&vehicle_brand='+vehicle_brand+'&plate_number='+plate_number+'&max_capacity='+max_capacity, {withCredentials: true}).subscribe(data => {
        resolve(data);
      }, err => {
        console.log(err);
      });
    });
  }

  transfer (source: string, destination: string, amount: string , description: string) {
    return new Promise(resolve => {
      this.http.get('http://localhost:65199/account/transfer?source='+source+'&destination='+destination+'&amount='+amount+'&description='+description, {withCredentials: true}).subscribe(data => {
        resolve(data);
      }, err => {
        console.log(err);
      });
    });
  }

  logout (username: string) {
    return new Promise(resolve => {
      this.http.get('http://nudrive.azurewebsites.net/drivers/signout?username='+username, {withCredentials: true}).subscribe(data => {
        resolve(data);
      }, err => {
        console.log(err);
      });
    });
  }

  profile (username: string) {
    return new Promise(resolve => {
      this.http.get('http://nudrive.azurewebsites.net/drivers/fetch?username='+username, {withCredentials: true}).subscribe(data => {
        resolve(data);
      }, err => {
        console.log(err);
      });
    });
  }


  fetchbooked (vehicleid: string) {
    return new Promise(resolve => {
      this.http.get('http://nudrive.azurewebsites.net/vehicles/fetchbooked?vehicle_id='+vehicleid,).subscribe(data => {
        resolve(data);
      }, err => {
        console.log(err);
      });
    });
  }

  isConnected(): boolean {
    let conntype = this.network.type;
    return conntype && conntype !== 'unknown' && conntype !== 'none';
  }

}



