import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams , AlertController, LoadingController} from 'ionic-angular';
import { MyservicesProvider } from './../../providers/myservices/myservices';
import { Storage } from '@ionic/storage';
import { LoginPage } from '../login/login';
/**
 * Generated class for the ProfilePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-profile',
  templateUrl: 'profile.html',
})
export class ProfilePage {
  loggedin = false;
profiledetails: any;
username: string;
phone: string;
password: string;
vehicle: string;
plate: string;
capacityy: string;
  constructor(public navCtrl: NavController,private alertCtr: AlertController,public loadingCtrl: LoadingController, public navParams: NavParams ,private Service: MyservicesProvider, private storage: Storage) {

    this.storage.get('usernamedriver').then(data=>
      {
        if(data)
        {
          this.loggedin = true;
          console.log("loogedin already");
          this.fetchdetails ();
        }
        else {
          this.loggedin = false;
          console.log("not logedin");
          this.presentAlert();
        }

        });


  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ProfilePage');

    this.fetchdetails () ;
  }

  presentAlert() {
    let alert = this.alertCtr.create({
      title: 'Not Logged In',
      subTitle: 'Please LogIn to view Profile',
      buttons: [ {
        text: 'Go to Login',
        role: 'destructive',
        handler: () => {
          console.log('Destructive clicked');
          this.navCtrl.pop();
          this.navCtrl.push(LoginPage);
        }
      }],
      enableBackdropDismiss: false
    });
    alert.present();
  }
  fetchdetails () {

    this.presentLoading();

    this.storage.get('username').then(data=>
      {

        this.Service.profile(data).then(data => {
          this.profiledetails = data;
          this.username = this.profiledetails.username;
          this.phone = this.profiledetails.phone;
          this.password = this.profiledetails.password;
          this.vehicle = this.profiledetails.vehicle_brand;
          this.plate = this.profiledetails.plate_number;
          this.capacityy = this.profiledetails.max_capacity;

          console.log(data);

                });

        });



  }

  presentLoading() {
    const loader = this.loadingCtrl.create({
      content: "Please wait...",
      duration: 3000
    });
    loader.present();
  }

}



